import moment from "moment";
import { useEffect, useState } from "react";
import DateTimeDisplay from "./DateTimeDisplay";
import useCountDown from "./useCountDown";

const ExpiredNotice = () => {
  return (
    <div className="bg-red-100 rounded px-3 py-1 text-gray-900">
      <span className="font-bold text-base text-red-700">Expired!!!</span>
      <p className="text-sm">Your exam time is expired.</p>
    </div>
  );
};

const ShowCounter = (props: {
  minutes: number;
  seconds: number;
  setSubmit: (status: boolean) => void;
}) => {
  const hrs = Math.floor(props.minutes / 60);
  const mins = props.minutes % 60;
  const dd = Math.floor(hrs / 24);

  const [days, setDays] = useState(dd);
  const [hours, setHours] = useState(hrs);
  const [minutes, setMinutes] = useState(mins);
  const [seconds, setSeconds] = useState(props.seconds);

  useEffect(() => {
    setTimeout(() => {
      if (
        days + hours + minutes + seconds > 0 &&
        days >= 0 &&
        hours >= 0 &&
        minutes >= 0 &&
        seconds >= 0
      ) {
        setSeconds(seconds > 0 ? seconds - 1 : 59);
      } else {
        props.setSubmit(true);
      }

      if (seconds === 0 && minutes > 0) {
        if (minutes - 1 === 0) {
          setSeconds(59);
        }
        setMinutes(minutes - 1);
      }
      if (minutes === 0 && seconds === 0 && hours > 0) {
        if (hours - 1 === 0) {
          setMinutes(59);
          setSeconds(59);
        }
        setHours(hours - 1);
      }
      if (minutes === 0 && seconds === 0 && hours === 0 && days > 0) {
        if (days - 1 === 0) {
          setHours(23);
          setMinutes(23);
          setSeconds(23);
        }
        setDays(days - 1);
      }
    }, 1000);
  });

  return (
    <div className="show-counter">
      <div className="flex flex-row items-center">
        <DateTimeDisplay value={days} type={"Days"} isDanger={days === 0} />
        <DateTimeDisplay value={hours} type={"Hours"} isDanger={hours === 0} />
        <DateTimeDisplay
          value={minutes}
          type={"Mins"}
          isDanger={minutes === 0}
        />
        <DateTimeDisplay
          value={seconds}
          type={"Seconds"}
          isDanger={seconds === 0}
        />
      </div>
    </div>
  );
};

const CountDownTimer = (props: {
  minutes: number;
  seconds: number;
  setSubmit: (status: boolean) => void;
}) => {
  return (
    <ShowCounter
      minutes={props.minutes}
      seconds={props.seconds}
      setSubmit={props.setSubmit}
    />
  );
};

export default CountDownTimer;
