const DateTimeDisplay = (props: {
  value: any;
  type: any;
  isDanger: boolean;
}) => {
  return (
    <div
      className={`${
        props.isDanger
          ? "text-gray-400 flex flex-col gap-0"
          : "text-white flex flex-col gap-0"
      } bg-gray-700 rounded ml-1 px-2 mt-1 -mb-1`}
    >
      <div className="text-lg font-extrabold">{props.value}</div>
      <div className="text-xs font-light -mt-1">{props.type}</div>
    </div>
  );
};

export default DateTimeDisplay;
